.pages{
      position: absolute;
      width: 440px;
      height: 480px;
      background: var(--theme);
      top: 6rem;
      left: 12rem;
      z-index: 100;
      box-shadow: 20px 20px 25px rgba(0,0,0,.5);
      padding: 2rem;
      border-radius: .6rem;
      visibility: hidden;
      opacity: 0;
}

.pages.show{
      animation: show_page .3s ease-in-out forwards
}

@keyframes show_page{
      0%{
            visibility: hidden;
            opacity: 0;
      }
      100%{
            visibility: visible;
            opacity: 1;
      }
}

.close_page{
      width: 38px;
      height: 38px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: 2rem;
      right: -3.8rem;
      background: var(--dark-200);
}

.close_page *{
      font-size: 2rem;
      color: var(--light);
}

.container{
      width: 100%;
      height: 100%;
      position:relative;
      margin-top: 3rem;
}

.friend_content,
.relation_content{
      background: var(--theme);
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
}

.friend_content{
      border-radius: .6rem;
}

.friend_content__col{
      width: 100%;
      height: 400px;
      padding: .5rem;
      background: rgba(0,0,0,.3);
      display: flex;
      flex-direction: column;
      gap: .5rem;
      border-radius: .6rem;
}

.friend_row{
      width: 280px;
      height: 70px;
      display: grid;
      grid-template-columns: 70px 1fr;
      gap: .5rem;
}

.friend_row img{
      width: 70px;
      height: 70px;
      border-radius: .3rem;
}

.friend_data{
      background: var(--dark-200);
      padding: 1rem;
      display: flex;;
      flex-direction: column;
      position: relative;
      border-radius: .3rem;
}

.friend_data h3{
      font-size: 1.4rem;
}

.friend_data span{
      color: var(--grys-200);
      font-size: 1.2rem;
      padding-left: 2rem;
      margin-top: .5rem;
}

.friend_data a{
      position: absolute;
      top: 50%;
      right: 1rem;
      transform: translateY(-50%);
      padding: .5rem 1.2rem;
      background: var(--theme);
      color: var(--light);
      border-radius: 999px;
      text-transform: uppercase;
}

.relation_content{
      display: flex;
      flex-direction: column;
      align-items: center;
}

.relation_avatar{
      width: 170px;
      height: 170px;
      border-radius: 50%;
      display: flex;
      box-shadow: 0 0 0 .5rem rgba(21,21,21,.5), 0 0 0 1.5rem rgba(21,21,21,.2), 0 0 0 2.5rem rgba(21,21,21,.2);
}

.relation_avatar img{
      width: 170px;
      height: 170px;
      border-radius: 50%;
}

.relation_content h3{
      color: var(--dark-200);
      margin-top: 3rem;
      font-size: 2rem;
}

.relation_content h4{
      margin-top: .5rem;
      font-size: 1.4rem;
}

.relation_about{
      margin-top: 2rem;
}

.relation_about p{
      font-size: 1.4rem;
      text-align: center;
}

.relation_content a{
      color: var(--light);
      text-transform: uppercase;
      background: var(--dark-200);
      border-radius: 999px;
      padding: .5rem 1.5rem;
      margin-top: 2rem;
}