@import url('https://fonts.googleapis.com/css2?family=Fira+Mono:wght@400;500;700&family=Oswald:wght@200;300;400;500;700&family=Pacifico&family=Xanh+Mono&display=swap');
:root{
  /** fonts */
  --ft-1: 'Pacifico', cursive;
  --ft-2: 'Oswald', sans-serif;
  --ft-3: 'Fira Mono', monospace;
  --ft-4: 'Xanh Mono', monospace;

  /** colors */
  --green: #45d985;
  --light: #fff;
  --red: #d90000;
  --alpha: rgba(0,0,0,.3);
  --grys-200: #707070;
  --grys-100: #979797;

  /** default theme */
  --theme: #8b0000;
  --dark-200: #151515;
  --dark-100: #1b1b1b;


  /** social color*/
  --facebook: #3b5995;
  --twitter: #1da1f2;
  --google: #dd4b39;
  --youtube: #c4302b;
  --vimeo: #1ab7ea;
  --dribbble: #ea4c89;
  --github: #333;
  --instagram: #c13584;
  --pinterest: #e60023;
  --flickr: #0063dc;
  --bitbucket: #205081;
  --linkedin: #0e76a8;
  --whatsapp: #12af0a;
  --tumblr: #35465c;
  --tiktok: #222;
  --discord: #5865f2;
  
  /** globals */
  font-family: var(--ft-3);
  font-size: 62.5%;
  color: var(--light);

}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: auto;
  min-height: 100vh;
  overflow-x: hidden;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--dark-100);
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
  font-family: var(--ft-2);
}

.bg_grad{
  display: inline-block;
  color: transparent;
  background: var(--gradient-45);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

::-webkit-scrollbar{
  width: .8rem;
  background: var(--dark-200);
}

::-webkit-scrollbar-thumb{
  background: var(--theme);
}