.content{
      width: 610px;
      height: 640px;
      background: var(--dark-200);
      position: relative;
}

.banner_content{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      border-left: solid 6rem var(--theme);
      display: flex;
      overflow: hidden;
      background: var(--light);
}

.banner_content img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      filter: grayscale(1);
}

.banner_content:before{
      width: 100%;
      height: 100%;
      background: linear-gradient(rgba(21,21,21,.8) 40%, rgba(21,21,21,1) 80%);
      top: 0;
      left: 0;
      z-index: 2;
}

.banner_content:after{
      width: 5px;
      height: 100%;
      background: linear-gradient(var(--theme) 50%, var(--light) 50%);
      top: 0;
      right: 0;
      z-index: 3;
}

.wrapper_user{
      position: relative;
      width: 100%;
      height: 100%;
      display: flex;
      z-index: 10;
}

.user_menu{
      width: 60px;
      height: 100%;
      flex-shrink: 0;
      position: relative;
}

.user_menu__buttons{
      width: 60px;
      height: auto;
      position: relative;
      margin-top: 8rem;
      transform: translateX(3rem);
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 1.5rem;
}

.user_menu__buttons button{
      width: 60px;
      height: 60px;
      border-radius: 50%;
      border: solid 2px var(--light);
      background: var(--theme);
      display: flex;
      align-items: center;
      justify-content: center;
}

.user_menu__buttons button *{
      font-size: 2.5rem;
      color: var(--light);
}

.user_content{
      flex-grow: 1;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 2rem;
}

.user_navigate{
      width: 100%;
      display: flex;
      height: 3rem;
      align-items: center;
      justify-content: flex-end;
      gap: 1rem;
}

.user_navigate a{
      line-height: 3rem;
      background: rgba(21,21,21,.8);
      padding: 0 1.5rem;
      border-radius: 999px;
      color: var(--light);
      text-transform:uppercase;
}

.user_data{
      margin-top: 3rem;
      display: flex;
      flex-direction: column;
      align-items: center;
}

.user_avatar{
      width: 220px;
      height: 220px;
      background: var(--dark-100);
      border-radius: 50%;
      box-shadow: 0 0 0 .5rem var(--theme), 0 0 0 1.5rem rgba(21,21,21,.2), 0 0 0 2.5rem rgba(21,21,21,.2);
      position: relative;
}

.user_avatar__img{
      width: 220px;
      height: 220px;
      overflow: hidden;
      display: flex;
      border-radius: 50%;
}

.online_dots{
      position: absolute;
      width: 14px;
      height: 14px;
      background: var(--light);
      border-radius: 50%;
      top: 2rem;
      right: 2.65rem;
      box-shadow: 0 0 0 4px var(--alpha);
}

.online_dots.green{
      background: var(--green);
}

.online_dots.red{
      background: var(--red);
}

.user_avatar span{
      background: var(--theme);
      color: var(--light);
      text-transform: uppercase;
      padding: .5rem 1.2rem;
      border-radius: 999px;
      position: absolute;
      top: 4rem;
      right: -2rem;
}

.user_data h1{
      margin-top: 3rem;
      font-size: 4rem;
      font-weight: 500;
}

.user_data p{
      padding: 1rem 3rem;
      background: var(--theme);
      border-radius: 999px;
      font-size: 1.4rem;
}

.user_footer{
      margin-top: 2rem;
      display: grid;
      grid-template-columns: 280px 1fr;
      gap: 2rem;
      width: 100%;
}

.user_footer__col{
      position: relative;
}

.wrapper_video{
      width: 280px;
      height: auto;
      position: relative;
}

.video_title{
      width: 260px;
      height: 32px;
      position: absolute;
      top: -1.6rem;
      left: 1rem;
      background: var(--theme);
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 1.2rem;
}

.user_id_box{
      width: 256px;
      height: 120px;
      background: var(--theme);
      border-right: solid .5rem var(--light);
      position: absolute;
      transform: translate(4rem, 3rem);
      padding: 0 1rem;
      display: grid;
      place-content: center;
}

.user_id_box h2,
.user_id_box h2 span{
      font-family: var(--ft-2);
      font-size: 10rem;
}

.user_id_box h2 span{
      color: var(--dark-100);
}

.video_box{
      width: 280px;
      height: 158px;
      background: #000;
      margin-top: 1.6rem;
}